/*
 * @internal
 */
export class MockClientSideWebPartStatusRenderer implements IClientSideWebPartStatusRenderer {

  public displayCalled: boolean = false;
  public clearCalled: boolean = false;
  public errorCalled: boolean = false;
  public clearErrorCalled: boolean = false;

  public displayLoadingIndicator(domElement: HTMLElement): void {
    this.displayCalled = true;
  }

  public clearLoadingIndicator(domElement: HTMLElement): void {
    this.clearCalled = true;
  }

  public renderError(domElement: HTMLElement, error: Error | string): void {
    this.errorCalled = true;
  }

  public clearError(domElement: HTMLElement): void {
    this.clearErrorCalled = true;
  }
}